import { Link } from "react-scroll";
import Comunicate from "./comunicate";
import './style/footer.scss'

export default function Footer(){
    return(
        <footer className="animate__animated animate__fadeIn">
            <Comunicate/>
            <div className="footer_content" >
                <ul>
                    <li> <Link to="about" spy={true} smooth={true} duration = {1000} href=""> </Link> </li>
                    <li> <Link to="carier" spy={true} smooth={true} duration = {1000} href=""></Link> </li>
                    <li> <Link to="carusel_two" spy={true} smooth={true} duration = {1000} href=""></Link> </li>
                    <li><Link to="carusel_one" spy={true} smooth={true} duration = {1000} offset={-100}  ></Link></li>
                    <li><Link to="images_one" spy={true} smooth={true} duration = {1000} offset={-100} ></Link></li>
                    <li> <Link to="obraz" spy={true} smooth={true} duration = {1000} href=""></Link> </li>
                    <li> <Link to="images_two" spy={true} smooth={true} duration = {1000} href=""></Link> </li>
                    <li><Link to="compen" spy={true} smooth={true} duration = {1000} offset={-100} ></Link></li>
                </ul>
                <h1> <span></span></h1>
            </div>
        </footer>
    )
}